<template lang="pug">
    div.search-content
        div.search-info
            div.search-bar
                input(type="text" v-model="keyword" placeholder="请搜索用户名或用户ID")
            div.search-button
                i.icon.iconfont(@click="search") &#xe694;
        div.search-result
            mt-loadmore(
                :top-method="loadTop"
                :bottom-method="loadBottom"
                :bottom-all-loaded="allLoaded"
                :autoFill="autoFill"
                ref="loadmore"
                @top-status-change="handleTopChange"
                @bottom-status-change="handleBottomChange"
            )
                mint-loadmore-top(:topStatus="topStatus" slot="top")
                template
                    user-list(:list="list" :allLoaded="allLoaded")
                mint-loadmore-bottom(:bottomStatus="bottomStatus" slot="bottom" v-show="!allLoaded")
</template>
<script>
import userList from '@/components/userList'
import queryString from 'querystring'
import { Toast, Indicator } from 'mint-ui'
import mintLoadmoreTop from '@/components/mintLoadmoreTop'
import mintLoadmoreBottom from '@/components/mintLoadmoreBottom'
export default {
    name: 'liveSearch',
    data() {
        return {
            keyword: '',
            list: '',
            has_next: '',
            page: 2,
            autoFill: false,
            allLoaded: true,
            topStatus: '',
            bottomStatus: ''
        }
    },
    components: {
        userList,
        mintLoadmoreTop,
        mintLoadmoreBottom
    },
    methods: {
        check() {
            if (!$.trim(this.keyword)) {
                Toast('请输入用户名或用户ID');
                this.list = '';
                return false;
            }
            else {
                return true;
            }
        },
        search(isRefresh = false) {
            if (this.check()) {
                Indicator.open();
                let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/userSearch' : this.api.userSearch();
                this.axios.get(`${axiosUrl}`, {
                    params: {
                        keyword: this.keyword
                    }
                }).then(res => {
                    Indicator.close();
                    if (res.data.status == 1) {
                        this.list = res.data && res.data.list;
                        this.has_next = res.data.has_next;
                        this.page = 2;
                    }
                    else {
                        Toast(res.data.error || '请求出错');
                    }
                    this.$nextTick(function() {
                        isRefresh && this.$refs.loadmore.onTopLoaded();
                    });

                })
                    .catch(err => {
                        Indicator.close();
                        Toast(err || '网络异常，请求失败');
                        isRefresh && this.$refs.loadmore.onTopLoaded();
                    });
            }
        },
        loadTop(id) {
            // 下拉刷新加载
            if (this.check()) {
                this.search(true);
            }
            else {
                this.$refs.loadmore.onTopLoaded();
            }

        },
        loadBottom() {
            // 无限加载
            let self = this;
            let axiosUrl = process.env.NODE_ENV == 'development' ? '/gm/api/userSearch' : self.api.userSearch();
            if (self.has_next) {
                self.axios.get(axiosUrl, {
                    params: {
                        p: self.page,
                        keyword: self.keyword
                    }
                }).then(res => {
                    setTimeout(() => {
                        if (res.data.status == 1) {

                            self.has_next = res.data.has_next;
                            self.page = self.page + 1;

                            if (res.data.list && res.data.list.length && res.data.list.length > 0) {
                                for (let i = 0; i < res.data.list.length; i++) {
                                    self.list.push(res.data.list[i]);
                                }
                            }
                        }
                        else {
                            Toast(res.data.error || '请求出错');
                        }

                        self.$nextTick(function() {
                            self.$refs.loadmore.onBottomLoaded();
                        });
                    }, 500);
                })
                    .catch(err => {
                        Toast(err || '网络异常，请求失败');
                        self.$refs.loadmore.onBottomLoaded();
                    });
            }
            else {
                setTimeout(function() {
                    Toast({
                        message: '无更多数据',
                        duration: 1000
                    });
                    self.$refs.loadmore.onBottomLoaded();
                }, 500);
                return false;
            }
        },
        handleTopChange(status) {
            // 获取下拉最新状态
            this.topStatus = status;
        },
        handleBottomChange(status) {
            // 获取上拉最新状态
            this.bottomStatus = status;
        }
    },
    watch: {
        keyword() {
            this.search();
        },
        list() {
            // 当页面数据发生改变,进行处理
            this.allLoaded = !Boolean(this.has_next);
        }
    }
}
</script>

<style lang="less" scoped>
@import '../../assets/css/variable.less';
.search-content {
    .search-info {
        height: 2.25rem;
        width: 100%;
        padding: .5rem .4rem 0 .2rem;
        display: flex;
        justify-content: space-between;
        background: #fff;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 1;
        .back-button {
            padding-bottom: 5px;
            .iconfont {
                font-size: 1rem;
                color: @color-theme;
            }
        }
        .search-bar {
            height: 1.25rem;
            width: 100%;
            padding: 0 1rem 0 1.5rem;

            input {
                height: 100%;
                width: 100%;
                display: block;
                color: #cfcfcf;
                font-size: .6rem;
                text-align: center;
                background: #f8f8f8;
                border: none;
                outline: none;
                border-radius: 30px;
            }
            input::-webkit-input-placeholder {
                color: #cfcfcf;
            }
        }
        .search-button {
            .iconfont {
                font-size: 1rem;
                color: @color-theme;
            }
        }
    }
    .search-result {
        margin-top: 2.25rem;
    }
}
</style>


